html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background: var(--bg);
  font-family: var(--font-regular);
  color: var(--text4);
}

h1, h2, h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --darkred: hsl(356, 100%, 37%);
  --darkblue: hsl(240, 67%, 25%);
  --bg: hsl(230, 17%, 14%);
  --card-hover: hsl(228, 25%, 27%);
  --top-bg: hsl(225, 100%, 98%);
  --text1: hsl(228, 34%, 66%);
  --text2: hsl(228, 12%, 44%);
  --text3: hsl(230, 17%, 14%);
  --text4: hsl(0, 0%, 100%);
  --toggle-bg-light: hsl(230, 22%, 74%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --card: hsl(228, 28%, 20%);
  --resume-start: hsl(300, 93%, 29%);
  --resume-bg: hsl(348, 97%, 39%);
  --grapefruit-middle: hsl(5, 77%, 71%);
  --grapefruit-end: hsl(329, 70%, 58%);
  --brazil-start: hsl(246, 94%, 31%);
  --brazil-middle: hsl(126, 92%, 29%);
  --brazil-end: hsl(66, 93%, 45%);
  --home-middle: hsl(189, 95%, 63%);
  --home-start: hsl(41, 64%, 60%);
  --home-end: hsl(34, 100%, 36%);
  --silly-bg: hsl(314, 100%, 54%);
  --bio-bg: hsl(34, 100%, 50%);
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.page-content {
  font-size: 1.5625rem;
}

.text {
  font-size: 1.375rem;
}

@font-face {
  font-family: "source_sans_proregular";
  src: url("fonts/sourcesanspro-regular-webfont.woff2") format("woff2");
}
@font-face {
  font-family: "source_sans_probold";
  src: url("fonts/sourcesanspro-bold-webfont.woff2") format("woff2");
}
:root {
  --font-regular: "source_sans_proregular";
  --font-bold: "source_sans_probold";
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.25rem;
  font-family: var(--font-regular);
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 1.25rem;
  color: var(--text1);
}

h1 {
  font-size: 2.5rem;
}

.contact {
  padding: 0.9375rem;
  height: 100%;
  background-color: var(--darkblue);
}
.contact:hover {
  cursor: pointer;
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3rem 1fr;
  gap: 0.3rem 0.5rem;
  border: none;
  margin: 0;
}
.toggle label {
  font-family: var(--font-bold);
  font-size: 0.875rem;
  align-self: center;
}
.toggle label[for=dime] {
  grid-column: 1;
  justify-self: end;
}
.toggle label[for=money] {
  grid-column: 3;
}
.toggle label:hover {
  cursor: pointer;
}
.toggle__wrapper {
  position: relative;
  grid-column: 2/3;
  grid-row: 1/2;
  height: 1.5rem;
}
.toggle input[type=radio] {
  opacity: 0;
  margin: 0;
  width: 1.1875rem;
  height: 1.1875rem;
  margin-right: -0.125rem;
}
.toggle input[type=radio]:hover {
  cursor: pointer;
}
.toggle__background {
  position: absolute;
  display: block;
  height: 100%;
  width: 100%;
  top: 0;
  border-radius: 0.75rem;
  overflow: hidden;
  background: linear-gradient(225deg, var(--toggle-bg-end) 0%, var(--toggle-bg-start) 98.02%);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  background-color: var(--bg);
  border-radius: 50%;
  transition: all 100ms ease-in;
}
.toggle #money:checked ~ .toggle__button {
  left: calc(100% - 21px);
  right: 3px;
}
.toggle #quiet-mode:checked ~ .toggle__button {
  left: 50%;
  right: auto;
  transform: translate(-50%);
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(5, 1fr);
  }
}

.card {
  background: var(--card);
  color: var(--text1);
  text-align: center;
  padding: 1.5625rem;
  height: 10.3125rem;
  border-radius: 0.3125rem;
  position: relative;
  overflow: hidden;
  transition: background 100ms ease-in-out;
  cursor: pointer;
}
.card:hover {
  background: var(--card-hover);
}
.card__title {
  font-size: 1.875rem;
  color: var(--top-bg);
  font-family: var(--font-regular);
  font-weight: 700;
}
.card__home {
  border-top: 0.3125rem solid var(--toggle-bg-start);
}
.card__resume {
  border-top: 0.3125rem solid var(--resume-bg);
}
.card__bio {
  border-top: 0.3125rem solid var(--bio-bg);
}
.card__proj::before {
  content: "";
  height: 0.3125rem;
  position: absolute;
  display: block;
  left: 0;
  top: 0;
  width: 100%;
  background: linear-gradient(225deg, var(--brazil-start), var(--brazil-middle), var(--brazil-end));
}
.card__silly {
  border-top: 0.3125rem solid var(--silly-bg);
}